:root {
  --standart-indent: 16px;
}
.profile {
  background-color: #fff;
  width: calc(var(--standart-indent) * 19);
  text-align: center;
  border-radius: 5px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  font-size: 16px;
  line-height: 1.2;
  }
  
  .description {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    flex-direction: column;
    align-items: center;
    background-color: antiquewhite;
    padding: calc(var(--standart-indent) * 2);
  }
  
  .avatar {
    display: flex;
    justify-items: center;
    border-radius: 50%;
    display: block;
    width: 200px;
    height: auto;
  }
  
  .name {
    font-weight: 700;
    font-size: 30px;
    margin: var(--standart-indent) 0 0 0;
  }
  
  .tag {
    margin: var(--standart-indent) 0 0 0;
    color: rgb(146, 146, 146);
  }
  
  .location {
    margin: var(--standart-indent) 0 0 0;
    color: rgb(146, 146, 146);
  }
  
  .stats {
    display: flex;
    margin: 0;
    padding: 0;
    list-style: none;
    justify-content: space-between;
    background-color: beige;
  }
  
  .item {
    width: 33.3%;
    padding: 15px;
  }
  
  .item:not(:last-child) {
    border-right: 1px solid #cfcfd0;
  }
  
  .label {
    display: block;
    margin-bottom: 5px;
    color: #768696;
  }
  
  .quantity {
    font-weight: 700;
  }

  